<template>
  <div>
    <navMedic/>
    <div class="container fluid bg-light">
      <h1 class="p-5 text-dark text-center">
        <b>Listado de Medicos</b>
      </h1>

      <b-modal id="modal-medic" ref="myModalRef" hide-footer title="Eliminar Médico">
        <div class="container">
          <div class="row">
            <p>¿Está seguro que desea eliminar al médico?</p>
            <div class="col-md-6 pl-0">
              <a
                href="#"
                @click="deleteById(selectedID)"
                class="btn btn-secondary btn-sm btn-block"
              >Confirmar</a>
            </div>
            <div class="col-md-6 pr-0">
              <a
                href="#"
                @click="$refs.myModalRef.hide()"
                class="btn btn-tertiary btn-sm btn-block"
              >Cancelar</a>
            </div>
          </div>
        </div>
      </b-modal>

      <b-row class="p-5">
        <b-col md="6" class="my-1">
          <b-form-group label-cols-sm="3" label="Buscar" class="mb-0">
            <b-input-group>
              <b-form-input v-model="filter" placeholder="Escriba para buscar"></b-form-input>
              <b-input-group-append>
                <b-button :disable="!filter" @click="filter=''">Limpiar</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col md="6" class="my-1">
          <b-form-group label-cols-sm="3" label="Ordenar por" class="mb-0">
            <b-input-group>
              <b-form-select v-model="sortBy" :options="sortOptions">
                <option slot="first" :value="null">-- Seleccione --</option>
              </b-form-select>
              <b-form-select v-model="sortDesc" :disabled="!sortBy" slot="append">
                <option :value="false">Asc</option>
                <option :value="true">Desc</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>
      </b-row>

    <b-table
        id="table-medic"
        show-empty
        hover
        small
        stacked="xl"
        fixed
        empty-filtered-text="No se encontrarón registros en su búsqueda"
        empty-text="No hay datos para mostrar"
        :items="medics"
        :fields="columns"
        :filter="filter"
        :current-page="currentPage"
        :per-page="10"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        @filtered="onFiltered"
      >
        <template v-slot:cell(tipoDocumento)="data">{{data.value.descripcion}}</template>
        
        <template v-slot:cell(nombres)="data">
          {{data.item.nombre}} {{data.item.apellido}}
        </template>

        <template v-slot:cell(especialidad)="data">{{data.value.descripcion}}</template>

        <template v-slot:cell(actions)="data">
          <router-link class="btn btn-warning mb-1 mr-1" :to="`/medico/${data.item.id}/editar`">
            <i class="fa fa-user-edit"></i>
          </router-link>
          <button class="btn btn-danger mb-1" type="button" @click="getId(data.item.id)">
            <i class="fa fa-trash-alt"></i>
          </button>
        </template>

      </b-table>

      <b-row class="p-5">
        <b-col md="6" class="my-1">
          <b-pagination
            size="md"
            :total-rows="totalRows"
            v-model="currentPage"
            :per-page="10"
            @input="find(currentPage)"
          ></b-pagination>
        </b-col>
      </b-row>

    </div>
    <Footer/>
  </div>
</template>

<script>
import { URL, bootbox } from "@/variables.js";
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import navMedic from "@/components/medic/navMedic.vue";
import axios from "axios";
import Footer from "@/components/Footer.vue";
export default {
  name: "medicos",
  data() {
    return {
      columns:[
        {
          key:"tipoDocumento",
          label:"Tipo Documento",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "numDocumento",
          label: "Número Documento"
        },
        {
          key:"nombres",
          label:"Nombres"
        },
        {
          key:"numTelefono",
          label:"Número de Telefono"
        },
        {
          key:"numCelular",
          label:"Número Celular"
        },
        {
          key:"especialidad",
          label:"Especialidad",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "actions",
          label: "Acciones"
        }
      ],
      medics: [],
      medic:[],
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      selectedID: null,
      totalRows: null,
      currentPage: 1,
    };
  },
  methods: {
    async find(currentPage) {
      axios
        .get(`${URL}/api/medico?page=${currentPage-1}`)
        .then(response => {
          this.medic = response.data.content
          this.totalRows = response.data.totalElements;
        })
        .catch(error => console.log(error));
    },
    async findAll() {
      axios
        .get(`${URL}/api/medicos`)
        .then(response => (this.medics = response.data))
        .catch(error => console.log(error));
    },
    deleteById(id) {
      axios
        .delete(`${URL}/api/medico/${id}`)
        .then(response => {
          this.$refs.myModalRef.hide();
          this.medics = this.medics.filter(medic => medic.id != id);
          bootbox.alert({
            title: "Eliminado Correctamente",
            message: response.data.mensaje,
            backdrop: true,
            locale: "es"
          });
        })
        .catch(error => {
          this.$refs.myModalRef.hide();
          bootbox.alert({
            title: "Error",
            message: error.response.data.mensaje,
            locale: "es",
            backdrop: true
          });
          console.log(error);
        });
    },
    getId(id) {
      this.selectedID = id;
      this.$refs.myModalRef.show();
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  },
  computed: {
    sortOptions() {
      return this.columns
        .filter(f => f.sortable)
        .map(f => {
          return {
            text: f.label,
            value: f.key
          };
        });
    },
  },
  mounted(currentPage){
    this.find(currentPage)
    this.findAll()
  },
  components: {
    navMedic,
    Footer
  }
};
</script>

